.list-container{
    max-width: 700px;
    min-width: 270px;
    margin: 10px 20px;
    padding: 0 05px 50px 0;
    
    z-index: 1;
}
.list-container ul{
    width: 90%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 0;
    list-style-type: none;
    justify-content: center;
    z-index: 2;
}
.list-container li{
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
    
}

